  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  gap: 24px;
  padding: 24px 4vw;
  box-sizing: border-box;
  font-family: Arial, sans-serif;
  background-color: #f0f4f8;
}

.page-button-layout {
  display: flex;
  flex-direction: row;

  gap: 16px;
  align-self: flex-start;
}

.house-picture {
  width: min(800px, 92vw);
  height: auto;
  border-radius: 8px;
}

.question-text {
  margin: 8px 0 0;
  font-weight: 700;
  font-size: clamp(22px, 4vw, 36px);
  text-align: center;
}

.buttonContainer {
  display: flex;
  flex-wrap: wrap;
  gap: 24px;
  justify-content: center;
  width: 100%;
}
