.graph-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  min-height: 100vh;
  background-color: #f0f4f8;
  font-family: 'Inter', sans-serif;
}

.page-button-layout {
  display: flex;
  flex-direction: row;

  gap: 16px;
  align-self: flex-start;
}

