  max-width: 1700px;
  margin-top: 30px; /*  */
}

/* ----------------------------------------------------
 * A. : /  
 * ---------------------------------------------------- */
.graph-area-wrap {
  display: flex;
  gap: 20px;
  flex-grow: 0;
  max-width: 1700px;
  flex-shrink: 1;
}

