import React from "react";
import "./house.css";
import PageSelectButton from "../../components/common/PageSelectButton";
import FurnitureSelectButton from "../../components/common/FurnitureSelectButton";

const House: React.FC = () => {

  return (
    <div className="house-page">
      <div className="page-button-layout">
        <PageSelectButton
        label=""
        to="/"
        color="#3498db"
        />
      </div>

      <img src="/title/.png" alt="" className="house-picture" />

      <h1 className="question-text"></h1>

      <div className="buttonContainer">
        <FurnitureSelectButton
          label=""
          to="/cup"
          color="#28a745"
        />
        <FurnitureSelectButton
          label=""
          to="/winter-window"
          color="#28a745"
        />
        <FurnitureSelectButton
          label=""
          to="/towel"
          color="#28a745"
        />
      </div>
    </div>
  );
};

export default House;