/* 1.  (CurrentHumidityDisplay) */
/* ========================================================= */

.graph-panel2 {
  padding: 20px;
  flex: 1;
  border: 1px solid #ddd;
  border-radius: 8px;
  background-color: #f9f9f9;
  min-width: 300px;
}

.graph-panel h3 {
  margin-top: 0;
  margin-bottom: 20px;
  color: #333;
  border-bottom: 2px solid #eee;
  padding-bottom: 10px;
}

/* ========================================================= */
/* 2. ReadRow  */
/* ========================================================= */

/*  (.read-row-container) */
.read-row-container {
  display: flex;
  gap: 10px;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 10px;
}

/*  (.read-row-label) */
.read-row-label {
  min-width: 200px; /*  */
  font-weight: bold;
  color: #555;
}

/*  (.read-row-value-group) */
.read-row-value-group {
  display: flex;
  align-items: center;
  gap: 8px;
}

/* ========================================================= */
/* 3.  */
/* ========================================================= */

/*  (.read-row-input-compact) */
.read-row-input-compact {
  width: 70px; /*  width: 70  */
  padding: 5px 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
  text-align: right;
  font-size: 14px;

  /*  */
  background-color: #eee;
  color: #333;
  cursor: default;
