___CSS_LOADER_EXPORT___.push([module.id, `/* ========================================================= */
/* 1.  */
/* ========================================================= */

.graph-panel1 {
  padding: 20px;
  flex: 1;
  border: 1px solid #ddd;
  border-radius: 8px;
  background-color: #f9f9f9;
  min-width: 600px;
}

.graph-panel h3 {
  margin-top: 0;
  margin-bottom: 20px;
  color: #333;
  border-bottom: 2px solid #eee;
  padding-bottom: 10px;
}

/* ========================================================= */
/* 2. Row  () */
/* ========================================================= */

.row-container {
  display: flex;
  gap: 10px;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 10px;
}

.row-label {
  /*  style={{ minWidth: 200 }}  */
  min-width: 200px;
  font-weight: bold;
  color: #555;
}

.row-controls {
  /*  style={{ display: "flex", alignItems: "center", gap: 8 }}  */
  display: flex;
  align-items: center;
  gap: 8px;
  flex-grow: 1; /*  */
}

/* ========================================================= */
/* 3.  */
/* ========================================================= */

/*  */
.input-compact {
  width: 60px; /*  */
  padding: 5px 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
  text-align: right;
  font-size: 14px;
}

/*  */
input[type="range"] {
  /* 1.  */
  flex-grow: 1; /*  */
  margin: 10px 0; /*  */
  width: auto; /* flex-grow */

  /* 2.  */
  -webkit-appearance: none; /* Chrome, Safari, Edge (WebKit) */
  -moz-appearance: none;    /* Firefox () */
  appearance: none;         /*  */

  /* 3.  */
  height: 8px; /*  */
  background: #ddd; /*  */
  border-radius: 4px;
  cursor: pointer; /*  */
}

/*  */
input:disabled,
input[readonly] {
  background-color: #eee;
  color: #aaa;
  cursor: not-allowed;
}

/* ========================================================= */
/* 4.  */
/* ========================================================= */

.text-alert {
  /*  */
  border-color: #e74c3c;
  box-shadow: 0 0 5px rgba(231, 76, 60, 0.5);
  background-color: #fbecec;
}

/* ========================================================= */
/* 5.  */
/* ========================================================= */

.btn-group {
  display: flex;
  justify-content: flex-end; /*  */
  gap: 10px;
  margin-top: 20px;
  padding-top: 10px;
  border-top: 1px dashed #eee;
}

.btn {
  padding: 8px 15px;
  border: 1px solid #3498db;
  border-radius: 4px;
  background-color: #3498db;
  color: white;
  cursor: pointer;
  transition: background-color 0.2s;
  font-size: 14px;
}

.btn:hover:not(.active) {
  background-color: #2980b9;
}

/*  */
.btn.active {
  background-color: #2ecc71;
  border-color: #2ecc71;
  font-weight: bold;
}

.btn.active:hover {
  background-color: #27ae60;
}`, "",{"version":3,"sources":["webpack://./src/components/graph/currentHumidityDisplay.css"],"names":[],"mappings":"AAAA,8DAA8D;AAC9D,mBAAmB;AACnB,8DAA8D;;AAE9D;EACE,aAAa;EACb,OAAO;EACP,sBAAsB;EACtB,kBAAkB;EAClB,yBAAyB;EACzB,gBAAgB;AAClB;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,WAAW;EACX,6BAA6B;EAC7B,oBAAoB;AACtB;;AAEA,8DAA8D;AAC9D,0CAA0C;AAC1C,8DAA8D;;AAE9D;EACE,aAAa;EACb,SAAS;EACT,mBAAmB;EACnB,8BAA8B;EAC9B,mBAAmB;AACrB;;AAEA;EACE,qCAAqC;EACrC,gBAAgB;EAChB,iBAAiB;EACjB,WAAW;AACb;;AAEA;EACE,qEAAqE;EACrE,aAAa;EACb,mBAAmB;EACnB,QAAQ;EACR,YAAY,EAAE,aAAa;AAC7B;;AAEA,8DAA8D;AAC9D,iBAAiB;AACjB,8DAA8D;;AAE9D,wBAAwB;AACxB;EACE,WAAW,EAAE,YAAY;EACzB,gBAAgB;EAChB,sBAAsB;EACtB,kBAAkB;EAClB,iBAAiB;EACjB,eAAe;AACjB;;AAEA,iBAAiB;AACjB;EACE,YAAY;EACZ,YAAY,EAAE,sBAAsB;EACpC,cAAc,EAAE,4BAA4B;EAC5C,WAAW,EAAE,oBAAoB;;EAEjC,+BAA+B;EAC/B,wBAAwB,EAAE,kCAAkC;EAC5D,qBAAqB,KAAK,4BAA4B;EACtD,gBAAgB,UAAU,OAAO;;EAEjC,uBAAuB;EACvB,WAAW,EAAE,YAAY;EACzB,gBAAgB,EAAE,aAAa;EAC/B,kBAAkB;EAClB,eAAe,EAAE,yBAAyB;AAC5C;;AAEA,uBAAuB;AACvB;;EAEE,sBAAsB;EACtB,WAAW;EACX,mBAAmB;AACrB;;AAEA,8DAA8D;AAC9D,qBAAqB;AACrB,8DAA8D;;AAE9D;EACE,oBAAoB;EACpB,qBAAqB;EACrB,0CAA0C;EAC1C,yBAAyB;AAC3B;;AAEA,8DAA8D;AAC9D,yBAAyB;AACzB,8DAA8D;;AAE9D;EACE,aAAa;EACb,yBAAyB,EAAE,eAAe;EAC1C,SAAS;EACT,gBAAgB;EAChB,iBAAiB;EACjB,2BAA2B;AAC7B;;AAEA;EACE,iBAAiB;EACjB,yBAAyB;EACzB,kBAAkB;EAClB,yBAAyB;EACzB,YAAY;EACZ,eAAe;EACf,iCAAiC;EACjC,eAAe;AACjB;;AAEA;EACE,yBAAyB;AAC3B;;AAEA,wBAAwB;AACxB;EACE,yBAAyB;EACzB,qBAAqB;EACrB,iBAAiB;AACnB;;AAEA;EACE,yBAAyB;AAC3B","sourcesContent":["/* ========================================================= */\r\n/* 1.  */\r\n/* ========================================================= */\r\n\r\n.graph-panel1 {\r\n  padding: 20px;\r\n  flex: 1;\r\n  border: 1px solid #ddd;\r\n  border-radius: 8px;\r\n  background-color: #f9f9f9;\r\n  min-width: 600px;\r\n}\r\n\r\n.graph-panel h3 {\r\n  margin-top: 0;\r\n  margin-bottom: 20px;\r\n  color: #333;\r\n  border-bottom: 2px solid #eee;\r\n  padding-bottom: 10px;\r\n}\r\n\