___CSS_LOADER_EXPORT___.push([module.id, `/* -------------------------------------------------- */
/*  */
/* -------------------------------------------------- */

.overall-layout {
    display: flex;
    padding: 20px;
    background-color: #f0f4f8;
    min-height: 100vh;
    box-sizing: border-box;
    position: relative;
    font-family: 'Inter', sans-serif;
    flex-direction: column;
    gap: 16px;
}

.page-button-layout {
  display: flex;
  flex-direction: row;
  gap: 16px;
  align-self: flex-start;
}

/* -------------------------------------------------- */
/* :  */
/* -------------------------------------------------- */

.experiment-layout {
    display: flex;
    gap: 0px;
    width: 100%;
    max-width: 1500px;
    margin: 0px auto 0px auto;
    justify-content: center;
}

.experimental-footage {
    display: flex;
    gap: 0px;
    width: 100%;
    max-width: 400px;
    margin: 0px auto 0px auto;
    justify-content: center;
    align-items: center;
}

.center-item-layout {
    display: flex;
    flex-direction: column;
    gap: 60px;
    width: 100%;
    max-width: 400px;
    margin:250px auto 0px auto;
    align-items: center;
    font-size: 20px;
}

.graph-canvas {
    padding: 20px;
    max-width: 600px;
    margin: 0px auto 50px auto;
    background-color: white;
    border-radius: 0.8vw;
    align-items: center;
}

/* -------------------------------------------------- */
/* :  */
/* -------------------------------------------------- */

.graph-controls {
    display: flex;
    gap: 20px;
    width: 100%;
    max-width: 1500px;
    margin: 0 auto;
    font-size: 24px;
}
.graph-controls > * {
    flex: 1;
    padding: 15px;
    border-radius: 12px;
    min-height: 250px;
}

/* -------------------------------------------------- */
/*  */
/* -------------------------------------------------- */

@media (max-width: 1024px) {
    /*  */
    .experiment-main-layout,
    .graph-controls {
        flex-direction: column;
        margin: 20px auto;
    }

    /*  */
    .experiment-main-layout > *,
    .graph-controls > * {
        flex: 1 1 100%;
    }
}`, "",{"version":3,"sources":["webpack://./src/pages/cup/cup.css"],"names":[],"mappings":"AAAA,uDAAuD;AACvD,WAAW;AACX,uDAAuD;;AAEvD;IACI,aAAa;IACb,aAAa;IACb,yBAAyB;IACzB,iBAAiB;IACjB,sBAAsB;IACtB,kBAAkB;IAClB,gCAAgC;IAChC,sBAAsB;IACtB,SAAS;AACb;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,SAAS;EACT,sBAAsB;AACxB;;AAEA,uDAAuD;AACvD,oBAAoB;AACpB,uDAAuD;;AAEvD;IACI,aAAa;IACb,QAAQ;IACR,WAAW;IACX,iBAAiB;IACjB,yBAAyB;IACzB,uBAAuB;AAC3B;;AAEA;IACI,aAAa;IACb,QAAQ;IACR,WAAW;IACX,gBAAgB;IAChB,yBAAyB;IACzB,uBAAuB;IACvB,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,SAAS;IACT,WAAW;IACX,gBAAgB;IAChB,0BAA0B;IAC1B,mBAAmB;IACnB,eAAe;AACnB;;AAEA;IACI,aAAa;IACb,gBAAgB;IAChB,0BAA0B;IAC1B,uBAAuB;IACvB,oBAAoB;IACpB,mBAAmB;AACvB;;AAEA,uDAAuD;AACvD,qBAAqB;AACrB,uDAAuD;;AAEvD;IACI,aAAa;IACb,SAAS;IACT,WAAW;IACX,iBAAiB;IACjB,cAAc;IACd,eAAe;AACnB;AACA;IACI,OAAO;IACP,aAAa;IACb,mBAAmB;IACnB,iBAAiB;AACrB;;AAEA,uDAAuD;AACvD,aAAa;AACb,uDAAuD;;AAEvD;IACI,6BAA6B;IAC7B;;QAEI,sBAAsB;QACtB,iBAAiB;IACrB;;IAEA,sBAAsB;IACtB;;QAEI,cAAc;IAClB;AACJ","sourcesContent":["/* -------------------------------------------------- */\r\n/*  */\r\n/* -------------------------------------------------- */\r\n\r\n.overall-layout {\r\n    display: flex;\r\n    padding: 20px;\r\n    background-color: #f0f4f8;\r\n    min-height: 100vh;\r\n    box-sizing: border-box;\r\n    position: relative;\r\n    font-family: 'Inter', sans-serif;\r\n    flex-direction: column;\r\n    gap: 16px;\r\n}\r\n\r\n.page-button-layout {\r\n  display: flex;\r\n  flex-direction: row;\r\n  gap: 16px;\r\n  align-self: flex-start;\r\n}\r\n\r\n/* -------------------------------------------------- */\r\n/* :  */\r\n/* -------------------------------------------------- */\r\n\r\n.experiment-layout {\r\n    display: flex;\r\n    gap: 0px;\r\n    width: 100%;\r\n    max-width: 1500px;\r\n    margin: 0px auto 0px auto;\r\n    justify-content: center;\r\n}\r\n\r\n.experimental-footage {\r\n    display: flex;\r\n    gap: 0px;\r\n    width: 100%;\r\n    max-width: 400px;\