import React from "react";
import "./savedHumidityDisplay.css";

interface ReadRowProps {
  label: string;
  value: number;
  unit: string;
}

const ReadRow: React.FC<ReadRowProps> = ({
  label,
  value,
  unit,
}) => (
  <div className="read-row-container">
    <label className="read-row-label">{label}</label>
    <div className="read-row-value-group">
      <input
        type="number"
        value={value.toFixed(1)}
        readOnly
        className="read-row-input-compact"
      />
      <span>{unit}</span>
    </div>
  </div>
);

interface SavedHumidityDisplayProps {
  // ---  () ---
  temperature2: number;
  saturationVapor2: number;
  vapor2: number;
  humidity2: number;
  waterDrop2: number;
}

/**
 * 
 */
const SavedHumidityDisplay: React.FC<SavedHumidityDisplayProps> = ({
  temperature2,
  saturationVapor2,
  vapor2,
  humidity2,
  waterDrop2,
}) => {
  return (
    <div className="graph-panel2">
      <h3></h3>

      {/*  */}
      <ReadRow label=" []" value={temperature2} unit="" />
      <ReadRow label=" [g/m]" value={saturationVapor2} unit="g/m" />
      <ReadRow label=" [g/m]" value={vapor2} unit="g/m" />
      <ReadRow label=" [%]" value={humidity2} unit="%" />
      <ReadRow label=" [g/m]" value={waterDrop2} unit="g/m" />
    </div>
  );
};

export default SavedHumidityDisplay;